/* General styles for the app */
.App {
  text-align: center;
  background-color: #121212; /* Dark background */
  color: #e0e0e0; /* Light text */
  min-height: 100vh; /* Ensure the app fills the entire viewport */
}

/* Layout styles */
.app-layout {
  display: flex;
  justify-content: flex-start; /* Space sections evenly */
  align-items: flex-start; /* Align sections at the top */
  margin-top: 20px; /* Add some space from the header */
}

/* Configuration section */
.configuration-section {
  flex: 1.0;
  padding: 20px;
  background-color: #1e1e1e; /* Dark background */
  border: 1px solid #333; /* Subtle border */
  border-radius: 8px;
}
.configuration-section label {
  font-weight: bold;
  color: #e0e0e0; /* Light text */
  margin-right: 10px; /* Add some space between label and input */
}
.configuration-section .input-group {
  display: flex;
  align-items: center;
  margin-bottom: 15px; /* Space between input groups */
}

/* Notes section */
.notes-section {
  display: inline-flex; /* Set width according to content while allowing internal wrapping */
  flex-direction: column; /* Stack the children vertically */
  padding: 10px;
  background-color: #1e1e1e; /* Dark background */
  border: 1px solid #333; /* Subtle border */
  border-radius: 8px;
  box-sizing: border-box; /* Include padding and border in width and height */
  font-family: Arial, sans-serif;
  color: #e0e0e0; /* Light text */
  width: fit-content; /* Fit tightly around the content */
}

/* Dropdown menus */
.dropdown-menus {
  font-family: Arial, sans-serif;
  margin-bottom: 20px;
  color: #e0e0e0; /* Light text */
}

.input-row {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
}

.input-row label {
  display: inline-block;
  width: 150px; /* Adjust width for label alignment */
  font-weight: bold;
  color: #e0e0e0; /* Light text */
  margin-right: 10px;
}

.input-row select,
.input-row input {
  flex: 1;
  padding: 8px;
  border-radius: 4px;
  border: 1px solid #555; /* Subtle border */
  background-color: #2b2b2b; /* Dark input background */
  color: #e0e0e0; /* Light text */
}

/* Canvas section */
.canvas-section {
  flex: 2;
  padding: 20px;
  background-color: white; /* Dark background */
  border: 1px solid #333; /* Subtle border */
  border-radius: 8px;
}

/* Dropdown menus and project info form */
.dropdown-menus label,
.project-info-form label {
  display: block;
  margin-bottom: 10px;
  color: #e0e0e0; /* Light text */
}

input,
select {
  flex: 1;
  padding: 8px;
  margin-bottom: 10px;
  border-radius: 4px;
  border: 1px solid #555; /* Subtle border */
  background-color: #2b2b2b; /* Dark input background */
  color: #e0e0e0; /* Light text */
}

/* Button styles */
button {
  padding: 10px 20px;
  background-color: #007bff; /* Bright blue for contrast */
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #0056b3; /* Darker blue for hover */
}

/* Heading styles */
h1, h3, h4 {
  text-align: center;
  margin-bottom: 20px;
  color: #e0e0e0; /* Light text */
}

/* Info section */
.info-section {
  flex: 0.6;
  display: flex;
  flex-direction: column;
  gap: 20px; /* Space between info boxes */
}

.info-box {
  background-color: white; /* Dark background */
  border: 1px solid #333; /* Subtle border */
  border-radius: 8px;
  padding: 10px;
  font-family: Arial, sans-serif;
  color: #e0e0e0; /* Light text */
  width: 300px;
}

.info-box h3 {
  margin-bottom: 10px;
  font-weight: bold;
  color: #e0e0e0; /* Light text */
}

.info-box div {
  margin-bottom: 5px;
  color: #e0e0e0; /* Light text */
}

.info-box label {
  font-weight: bold;
  color: #e0e0e0; /* Light text */
  margin-right: 5px;
}

.info-box ul {
  padding-left: 20px;
  color: #e0e0e0; /* Light text */
}

.info-box span {
  color: #b0b0b0; /* Slightly dimmed text */
}

/* Details section */
.details-section {
  flex: 1;
  padding: 20px;
}

.details-card {
  background: white; /* Dark background */
  border: 1px solid #333; /* Subtle border */
  border-radius: 8px;
  padding: 20px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  color: black; /* Light text */
}

.details-card h3 {
  margin-bottom: 20px;
  font-size: 1.5rem;
  color: black; /* Light text */
}

.details-card p,
.details-card h4 {
  margin-bottom: 10px;
  color: black; /* Light text */
}
